.container {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background: linear-gradient(#000000, #713762);
}

.title {
    color: #ffffff;
    font-size: 80px;
    text-align: center;
    animation: fliptitle 2s;
    margin-top: 20px;
}

@keyframes fliptitle {
    from {
        transform: rotateY(50deg);
    }
    to {
        transform: rotateY(0deg);
    }
}

.containerInput {
    background-color: rgba(255, 255, 255, 0.2);
    padding: 15px;
    margin: 31px 0;
    display: flex;
    border-radius: 8px;
    box-shadow: 1px 3px 8px rgba(0, 0, 0, 0);
}

.containerInput input {
    background-color: transparent;
    border: 0;
    font-size: 18px;
    color: #ffffff;
    outline: none;
    margin-right: 8px;
}

.button {
    background-color: transparent;
    border: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: transform 0.1s;
}

.button:hover {
    transform: scale(1.3);
}

.main {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: #ffffff;
    width: 500px;
    border-radius: 8px;
}
.main h2 {
     margin: 8px 0;
     font-size: 30px;
}

.main span{
    margin-bottom: 8px;
    font-weight: bold;
}

@media (max-width: 620px) {
    .title {
        font-size: 50px;
        text-align: center;
    }

    .main h2 {
        font-size: 28px;
    }

    .main {
        width: 80%;
        min-height: 100px;
    }
}